<template>
	<view>
		<view class="tabs-box">
			<view class="one-nav" :class="isShowLeft" @tap="swiperChange(0)">推荐</view>
			<view class="one-nav" :class="isShowRight" @tap="swiperChange(1)">资讯</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {

			}
		},
		computed: {
			// 是否显示左右边高亮
			isShowLeft() {
				return this.currentIndex === 0 ? 'nav-actived' : ''
			},
			isShowRight() {
				return this.currentIndex === 1 ? 'nav-actived' : ''
			}
		},
		methods: {
			swiperChange(index) {
				this.currentIndex = index
			}
		}
	}
</script>

<style lang="scss" scoped>
	.tabs-box {
		width: 750upx;
		position: absolute;
		z-index: 1;
		left: 0;
		top: 460upx;
		display: flex;
		flex-direction: row;
		justify-content: center;

		.one-nav {
			height: 80upx;
			width: 110upx;
			color: #9B9B9B;
			font-size: 36upx;
			text-align: center;
			font-weight: blod;
			line-height: 80upx;

			&.nav-actived {
				color: #0050FF;
			}
		}
	}
</style>
